.home {
    min-height: 150vh;
    font-family: 'Architects Daughter', cursive;
}

section {
    text-align: center;
    position: relative;
    overflow: hidden;
    padding: 1em;
}

section p {
    color: white;
}

.home-header {
    background: url("https://img6.gram.pl/jnwy20200415113437188kfnx.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    height: 100vh;
    text-align: start;
}

.home-header h1 {
    padding-top: 2em;
}

.home-header h2 {
    padding-top: 5em;
    padding-bottom: 3em;
    color: white;
}

.home-header h1, .home-header h2 {
    margin: 0;
    padding-left: 5%;
    text-shadow: 0 0.1em 0.1em black;
}

.home-header img {
    position: absolute;
    top: 30%;
    right: 5%;
}



/* ABOUT */
.home-about {
    min-height: 50vh;
    padding: 2em;
    background: url("https://wallpaperaccess.com/full/983279.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.home-about h2 {
    padding: 0 1em;
}

.home-about .home-about__container {
    display: flex;
    justify-content: center;
}

.home-about .home-about__container-paragraphs, .home-about .home-about__container-image {
    padding: 0.5em;
}

.home-about .home-about__container-paragraphs p {
    padding: 0 0.5em;
}

.home-about .home-about__container-image {
    min-width: 40%;
}

.home-about .home-about__container-image img {
    height: 100%;
    width: 100%;
    border-radius: 10px;
    object-fit: cover;
}


/* SHOOP */
.home-shoop {
    background: url("https://kbdevstorage1.blob.core.windows.net/asset-blobs/18902_en_1");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 80vh;
}

.home-shoop img {
    position: absolute;
}

.home-shoop .sword {
    bottom: 10%;
    left: 7%;
    width: 25%;
}

.home-shoop .skull {
    bottom: 30%;
    left: 40%;
    width: 20%;
}

.home-shoop .diego {
    top: 15%;
    right: 0;
    width: 30%;
}

.home-shoop button {
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translate(-50%, -50%);
}


/* CHARACTERS */
.home-characters {
    background: url("https://wallpaperaccess.com/full/983279.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 80vh;
}

.home-characters__container {
    display: flex;
    justify-content: center;
    padding: 3em 0;
}

.home-characters__container h2 {
    padding: 1em 0;
}

.home-characters__container span {
    margin-right: 1em;
}

.home-characters button {
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translate(-50%, -50%);
}


/* CONTACT */
.home-contact {
    background: url("https://i.pinimg.com/originals/c7/d6/44/c7d6443c3e206f73ff8c105b5e7d1abb.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 3em 0;
}

.home-contact button {
    margin-top: 2em;
}